import api from '@/common/vmeitime-http/'
export default {
	data() {
		return {
			inputid: 1,
			allmoney: 0,
			carouselList: [
				{url: '../../static/temp/fxbanner.jpg'},
				{url: '../../static/temp/fxbanner.jpg'},
				{url: '../../static/temp/fxbanner.jpg'},
			],
			moneyinput: '',
			username: '',
			userId: '',
			mobile: ''
		}
	},
	onLoad(options) {
		this.userId = options.id;
		uni.showModal({
		  title: '',
		  content: '您还不是分销商，请先提交申请',
		  success: function(res) {
			if (!res.confirm) {
				uni.navigateBack()
			}
		  }
		})
	},
	onPullDownRefresh(){
		this.init(true)
	},
	onShareAppMessage(res) {
	  return {
		title: '',
		imageUrl: '/static/temp/banner3.jpg',
		path: '/pages/index/index'
	  }
	},
	methods: {
		subdrawbtn() {
			if(this.username == '') {
				uni.showToast({
					title: '请输入姓名',icon: 'none'
				});
				return false;
			} else if(this.mobile == '' || !/^1[3|4|5|6|7|8|9][0-9]\d{8}$/.test(this.mobile)) {
				uni.showToast({title: '请输入正确的手机号吗', icon: 'none'})
				return false;
			}
			let userInfo = uni.getStorageSync('userInfo')
			let userId = uni.getStorageSync('userId')
			let data = {
				 headImage:userInfo.avatarUrl,
				 nickName:userInfo.nickName,
				 name:this.username,
				 mobile:this.mobile,
				 level:0,
				 retailUserId: userId
			}
			api.httppostfx('/retailUser/add',data).then((res) => {
				let respons = res.data
				if (respons.code == 200) {
					uni.showToast({title: '提交申请成功！', icon: 'none'})
					setTimeout(function() {
						uni.navigateBack()
					}, 1000);
				} else {
					uni.showToast({title: respons.errmsg, icon: 'none'})
				}
				uni.hideLoading()
			})
		}
	}
}
